export class DateHelper {
	constructor() {
		this.ampm = ['am', 'pm'];
	}

	parseSelections(format) {
		let selections = [];
		let formatTokens = format.split(/[^A-Za-z]/);
		let start = 0;
		let end = 0;
		for (let token of formatTokens) {
			if (token) {
				end += token.length;
				selections.push([start, end]);
				start = end + 1;
				end = start;
			} else {
				start++;
				end++;
			}
		}
		return selections;
	}

	parseDate(value, format) {
		if (!value) {
			return null;
		}
		value = value.replace(this.ampm[0], 'am');
		value = value.replace(this.ampm[1], 'pm');
		const formatTokens = format.split(/[^A-Za-z]/).filter(t => t);
		const dateTokens = value.split(/[^A-Za-z0-9]/).filter(t => t);
		const values = [0, 0, 0, 0, 0, 0, 0];

		const parser = {
			dd: (value) => values[2] = value,
			d: (value) => values[2] = value,
			MM: (value) => values[1] = (value - 1),
			M: (value) => values[1] = (value - 1),
			yyyy: (value) => values[0] = value,
			yy: (value) => {
				value = +value;
				const year = new Date().getFullYear();
				if (value + 2000 - year < 20) {
					value += 2000;
				} else {
					value += 1900;
				}
				values[0] = value;
			},
			HH: (value) => values[3] = value,
			H: (value) => values[3] = value,
			hh: (value) => values[3] = value,
			mm: (value) => values[4] = value,
			ss: (value) => values[5] = value,
			SSS: (value) => values[6] = value,
			SS: (value) => values[6] = value,
			S: (value) => values[6] = value,
			a: (value) => {
				if (format.indexOf('hh') !== -1 || format.indexOf('h') !== -1) {
					const hour = values[3];
					if (value === 'pm' && hour < 12) {
						values[3] = hour + 12;
					}
					if (value === 'am' && hour === 12) {
						values[3] = 0;
					}
				}
			}
		};

		for (let i = 0; i < formatTokens.length; i++) {
			let formatToken = formatTokens[i];
			let dateToken = dateTokens[i];
			let f = parser[formatToken];
			if (f) {
				if (dateToken === 'am' || dateToken === 'pm') {
					f(dateToken);
				} else {
					f(parseInt(dateToken, 10) || 0);
				}
			}
		}
		// return new Date(values[0], values[1], values[2], values[3], values[4], values[5], values[6])
		const d = new Date(values[0], values[1], values[2], values[3], values[4], values[5], values[6]);
		const result =  (d instanceof Date && !isNaN(d.getTime())) ? d : null;
		return result;
	}

	formatDate(date, format) {
		if (!date) {
			return '';
		}
		let formatN = (value) => {
			return value < 10 ? '0' + value : value;
		};
		let formatter = {
			dd: () => formatN(date.getDate()),
			d: () => date.getDate(),
			MM: () => formatN(date.getMonth() + 1),
			M: () => date.getMonth() + 1,
			yyyy: () => date.getFullYear(),
			yy: () => String(date.getFullYear()).substr(2, 2),
			HH: () => formatN(date.getHours()),
			hh: () => {
				const hour = date.getHours();
				return formatN(hour % 12 === 0 ? 12 : hour % 12);
			},
			mm: () => formatN(date.getMinutes()),
			ss: () => formatN(date.getSeconds()),
			SSS: () => formatN(date.getMilliseconds()),
			SS: () => formatN(date.getMilliseconds()),
			S: () => date.getMilliseconds(),
			a: () => {
				const hour = date.getHours();
				return hour < 12 ? this.ampm[0] : this.ampm[1];
			}
		};
		return format.replace(/dd|d|M{1,4}|yyyy|yy|HH|hh|mm|ss|a|S{1,3}|E{3,4}/g, (match) => {
			let f = formatter[match];
			return f ? f() : match;
		});
	}

	setAmPm(ampm) {
		this.ampm = ampm;
	}
}
export default new DateHelper();



