import PropTypes from 'prop-types';
import FocusedBase from './FocusedBase';

class FieldBase extends FocusedBase {
    constructor(props) {
        super(props);
        Object.assign(this.state, {
            invalid: props.invalid,
            el: null
        })
    }
    componentDidMount() {
        if (this.context && this.context.fieldAdd) {
            this.context.fieldAdd(this);
            this.backContext = this.context;
        }
    }
    componentDidUpdate(prevProps) {
        if (prevProps.invalid !== this.props.invalid) {
            this.setState({ invalid: this.props.invalid });
        }
    }
    componentWillUnmount() {
        if (this.context && this.context.fieldRemove) {
            this.context.fieldRemove(this);
        } else if (this.backContext && this.backContext.fieldRemove) {
            this.backContext.fieldRemove(this);
        }
    }
    getFieldName() {
        const name = this.props.name;
        if (name) {
            return name;
        }
        if (this.context && this.context.fieldName) {
            return this.context.fieldName;
        } else {
            return null;
        }
    }
    renderField(context) {
        return null;
    }
    renderContext(context) {
        return this.renderField(context)
    }
    // render() {
    //     return null;
    // }
}
// FieldBase.contextTypes = Object.assign({}, LocaleBase.contextTypes, {
//     fieldChange: PropTypes.func,
//     fieldFocus: PropTypes.func,
//     fieldBlur: PropTypes.func,
//     fieldAdd: PropTypes.func,
//     fieldRemove: PropTypes.func,
//     fieldName: PropTypes.string
// })
FieldBase.propTypes = Object.assign({}, FocusedBase.propTypes, {
    name: PropTypes.string,
    invalid: PropTypes.bool,
    validateOnCreate: PropTypes.bool,
    validateOnBlur: PropTypes.bool,
    validateOnChange: PropTypes.bool
})
FieldBase.defaultProps = Object.assign({}, FocusedBase.defaultProps, {
    invalid: false,
    validateOnCreate: true,
    validateOnBlur: false,
    validateOnChange: true,
    isField: true
})
export default FieldBase;


