import React from 'react';
import PropTypes from 'prop-types';
import classHelper from '../base/ClassHelper';
import FocusedBase from '../base/FocusedBase';

class LinkButton extends FocusedBase {
    constructor(props) {
        super(props);
        Object.assign(this.state, {
            selected: props.selected,
            focused: false
        })
    }
    componentDidUpdate(prevProps) {
        if (prevProps.selected !== this.props.selected) {
            this.setState({ selected: this.props.selected })
        }
    }
    focus() {
        this.setFocused(this.el, true)
    }
    blur() {
        this.setFocused(this.el, false)
    }
    innerCls() {
        const { size, plain, outline, className } = this.props;
        return classHelper.classNames(['l-btn f-inline-row f-content-center', 'l-btn-' + size, className, {
            'l-btn-plain': plain,
            'l-btn-outline': outline,
            'l-btn-selected': this.state.selected,
            'l-btn-plain-selected': this.state.selected && plain,
            'l-btn-disabled': this.isDisabled(),
            'l-btn-plain-disabled': this.isDisabled() && plain,
            'l-btn-focus': this.state.focused
        }])
    }
    btnIconCls() {
        let cls = 'l-btn-icon';
        if (this.props.iconCls) {
            cls += ' ' + this.props.iconCls;
        }
        return cls;
    }
    btnLeftCls() {
        let cls = 'l-btn-left';
        if (this.props.iconCls) {
            cls += ' l-btn-icon-' + this.props.iconAlign;
        }
        return cls;
    }
    btnTextCls() {
        return classHelper.classNames(['l-btn-text', {
            'l-btn-empty': this.isEmpty()
        }]);
    }
    isDisabled() {
        return this.props.disabled;
    }
    isEmpty() {
        return !this.props.text && !this.props.children;
    }
    text() {
        if (this.isEmpty()) {
            return null;
        } else {
            return this.props.text || this.props.children;
        }
    }
    handleClick(event) {
        const { disabled, href, toggle } = this.props;
        event.stopPropagation();
        if (disabled) {
            event.preventDefault();
            return false;
        }
        if (!href) {
            event.preventDefault();
        }
        if (toggle) {
            this.setState({ selected: !this.state.selected })
        }
        this.props.onClick(event);
    }
    renderInners() {
        return [
            <span key="text" className={this.btnTextCls()}>{this.text()}</span>,
            <span key="icon" className={this.btnIconCls()}></span>
        ]
    }
    renderOthers() {
        return null;
    }
    renderButton() {
        return (
            <a
                style={this.props.style}
                className={this.innerCls()}
                ref={el => this.el = el}
                href={this.props.href || '#'}
                tabIndex={this.props.tabIndex}
                onFocus={this.focus.bind(this)}
                onBlur={this.blur.bind(this)}
                onClick={this.handleClick.bind(this)}
            >
                <span className={this.btnLeftCls()}>
                    {this.renderInners()}
                </span>
                {this.renderOthers()}
            </a>
        )
    }
    renderContext() {
        return this.renderButton();
    }
}
LinkButton.propTypes = Object.assign({}, FocusedBase.propTypes, {
    disabled: PropTypes.bool,
    toggle: PropTypes.bool,
    selected: PropTypes.bool,
    outline: PropTypes.bool,
    plain: PropTypes.bool,
    text: PropTypes.string,
    iconCls: PropTypes.string,
    iconAlign: PropTypes.string,
    size: PropTypes.string,
    href: PropTypes.string,
    className: PropTypes.string,
    style: PropTypes.object,
    onClick: PropTypes.func
})
LinkButton.defaultProps = Object.assign({}, FocusedBase.defaultProps, {
    disabled: false,
    toggle: false,
    selected: false,
    outline: false,
    plain: false,
    iconAlign: 'left',
    size: 'small',
    onClick() { }
})
export default LinkButton


