import React from 'react';
import PropTypes from 'prop-types';
import LocaleBase from '../base/LocaleBase';

class ButtonGroup extends LocaleBase {
    constructor(props) {
        super(props);
        this.state = {
            selectedIndex: props.selectedIndex
        }
    }
    componentDidMount() {
        let index = -1;
        if (this.props.selectedIndex >= 0) {
            index = this.props.selectedIndex;
        } else {
            index = this.props.children.findIndex(button => button.props.selected);
        }
        this.setState({ selectedIndex: index })
    }
    componentDidUpdate(prevProps) {
        if (prevProps.selectedIndex !== this.props.selectedIndex) {
            this.setState({
                selectedIndex: this.props.selectedIndex
            })
        }
    }
    renderContext() {
        let buttons = this.props.children;
        if (this.props.selectionMode === 'single') {
            buttons = React.Children.map(this.props.children, (button, index) => {
                return React.cloneElement(button, {
                    selected: index === this.state.selectedIndex,
                    onClick: () => {
                        this.setState({ selectedIndex: index });
                        button.props.onClick();
                    }
                })
            })
        }
        return (
            <span className="button-group f-inline-row" style={this.props.style}>
                {
                    buttons
                }
            </span>
        )
    }
}
ButtonGroup.propTypes = {
    selectionMode: PropTypes.oneOf(['single', 'multiple']),
    selectedIndex: PropTypes.number
}
ButtonGroup.defaultProps = {
    selectionMode: 'multiple',
    selectedIndex: -1
}
export default ButtonGroup


