import LocaleContext from './LocaleContext';

const FieldContext = Object.assign({}, LocaleContext, {
    fieldChange: null,
    fieldFocus: null,
    fieldBlur: null,
    fieldAdd: null,
    fieldRemove: null,
    fieldName: null
})
export default FieldContext


