import PropTypes from 'prop-types';
import LocaleBase from './LocaleBase';

class FocusedBase extends LocaleBase {
    constructor(props) {
        super(props);
        this.state = {
            focused: false
        }
    }
    setFocused(el, value) {
        if (el) {
            const isFocused = this.state.focused;
            if (value) {
                el.focus();
                this.setState({ focused: true }, () => {
                    if (!isFocused){
                        this.props.onFocus();
                    }
                    if (this.context && this.context.fieldFocus) {
                        this.context.fieldFocus(this);
                    }
                })
            } else {
                el.blur();
                this.setState({ focused: false }, () => {
                    if (isFocused){
                        this.props.onBlur();
                    }
                    if (this.context && this.context.fieldBlur) {
                        this.context.fieldBlur(this)
                    }
                })
            }
        }
    }
}
FocusedBase.propTypes = Object.assign({}, LocaleBase.propTypes, {
    tabIndex: PropTypes.number,
    onFocus: PropTypes.func,
    onBlur: PropTypes.func
})
FocusedBase.defaultProps = Object.assign({}, LocaleBase.defaultProps, {
    onFocus() { },
    onBlur() { }
})
export default FocusedBase


