import React from 'react';
import PropTypes from 'prop-types';
import LocaleBase from '../base/LocaleBase';
import { domHelper, classHelper } from '../base';

class TimeClock extends LocaleBase {
    constructor(props) {
        super(props);
        this.state = {
            data: this.getData(props.type),
            value: props.value,
            radius: 0,
            width: 0,
            height: 0
        }
    }
    componentDidMount() {
        setTimeout(() => {
            const width = domHelper.outerWidth(this.el);
            const height = domHelper.outerHeight(this.el);
            const size = Math.min(width, height) - 20;
            this.setState({
                radius: size / 2,
                width: size,
                height: size
            })
        })
    }
    componentDidUpdate(prevProps) {
        if (prevProps.value !== this.props.value) {
            this.setState({ value: this.props.value })
        }
        if (prevProps.type !== this.props.type) {
            this.setState({ data: this.getData(this.props.type) })
        }
    }
    getData(type = 'hour') {
        let data = [];
        if (type === 'hour') {
            for (let i = 0; i < 12; i++) {
                data.push(String(i))
            }
            data[0] = '12'
        } else {
            for (let i = 0; i < 60; i += 5) {
                data.push(i < 10 ? '0' + i : String(i))
            }
            data[0] = '00'
        }
        return data;
    }
    handleItemClick(value) {
        setTimeout(() => {
            value = parseInt(value, 10);
            this.setState({ value: value }, () => {
                this.props.onSelect(value)
            })
        })
    }
    handStyle() {
        const { value } = this.state;
        const { type, hour24, hourDistance } = this.props;
        const angular = value / (type === 'hour' ? 12 : 60) * 360;
        const style = {
            transform: `rotate(${angular}deg)`
        }
        if (hour24 && type === 'hour') {
            if (value === 0) {
                style.top = hourDistance[0] + 'px';
            } else if (value <= 12) {
                style.top = hourDistance[1] + 'px';
            }
        }
        return style;
    }
    itemClasses(value) {
        return classHelper.classNames('item f-column f-content-center', {
            'item-selected': parseInt(value, 10) === parseInt(this.state.value, 10)
        })
    }
    itemStyle(value, is24 = false) {
        const { type, hour24, hourDistance } = this.props;
        const distance = hourDistance[1] - hourDistance[0];
        const radius = this.state.radius - (hour24 && type === 'hour' ? (is24 ? 0 : distance) : 0);
        const angular = parseInt(value, 10) / (type === 'hour' ? 12 : 60) * 360 * Math.PI / 180;
        const x = (radius - 20) * Math.sin(angular);
        const y = -(radius - 20) * Math.cos(angular);
        return {
            transform: `translate(${x}px,${y}px)`
        }
    }
    renderItems() {
        const { type, hour24 } = this.props;
        const { data } = this.state;
        const style = {
            width: this.state.width,
            height: this.state.height,
            marginLeft: -this.state.width / 2,
            marginTop: -this.state.height / 2
        }
        const getHour24 = (value) => {
            let itemValue = parseInt(value, 10);
            itemValue += 12;
            if (itemValue === 24) {
                itemValue = '00';
            }
            return itemValue;
        }

        return (
            <div className="clock" style={style}>
                <div className="center"></div>
                <div className="hand" style={this.handStyle()}>
                    <div className="drag"></div>
                </div>
                {
                    data.map((value, index) => (
                        <div key={index}
                            className={this.itemClasses(value)}
                            style={this.itemStyle(value)}
                            onClick={this.handleItemClick.bind(this, value)}
                        >
                            {value}
                        </div>
                    ))
                }
                {
                    hour24 && type === 'hour' &&
                    data.map((value, index) => (
                        <div key={index}
                            className={this.itemClasses(getHour24(value))}
                            style={this.itemStyle(getHour24(value), true)}
                            onClick={this.handleItemClick.bind(this, getHour24(value))}
                        >
                            {getHour24(value)}
                        </div>
                    ))
                }
            </div>
        )
    }
    renderContext() {
        return (
            <div
                className="clock-wrap f-full f-column f-content-center"
                ref={el => this.el = el}
            >
                {
                    this.renderItems()
                }
            </div>
        )
    }
}
TimeClock.propTypes = Object.assign({}, LocaleBase.propTypes, {
    value: PropTypes.number,
    type: PropTypes.oneOf(['hour', 'minute'])
})
TimeClock.defaultProps = Object.assign({}, LocaleBase.defaultProps, {
    value: 0,
    type: 'hour',
    onSelect(value) { }
})
export default TimeClock


